<template>
  <div>
    <template>
      <v-form
        ref="form"
        v-model="valid"
        lazy-validation
      >

        <v-text-field
          v-model="title"
          :counter="50"
          :rules="titleRules"
          label=""
          solo
          required
        ></v-text-field>

        <v-textarea
          v-model="content"
          label=""
          rows="10"
          row-height="60"
          solo
        ></v-textarea>

        <div style="display: flex; justify-content: center;">
          <v-btn @click="goBack()" style="background-color: #979797; color: #FFFFFF; margin: 0 10px;"></v-btn>
          <v-btn @click="submit()" style="background-color: #797BF8; color: #FFFFFF; margin: 0 10px;"></v-btn>
        </div>
      </v-form>
    </template>
  </div>
</template>

<script>
  export default {
    name: 'CreateArticleForm',

    data: function() {
      return {
        valid: true,
        communityId: '',
        title: '',
        titleRules: [
          v => !!v || ' .',
          v => (v && v.length <= 50) || ' 50  .'
        ],
        content: '',
      }
    },

    methods: {
      goBack () {
        this.$router.push({ name: 'CommunityListItem', params: {communityId: this.communityId} })
      },
      submit () {
        const articleItem = {
          title: this.title,
          content: this.content,
        }
        if (articleItem.title) {
          this.$axios({
            method: 'post',
            url: `http://localhost:8080/api/v1/communities/${this.communityId}/articles`,
            data: articleItem,
          })
          .then(res => {
            console.log(res)
            this.title = ''
            this.content = ''
            this.$router.push({ name: 'Index' })
          })
          .catch(err => {
            console.log(articleItem)
            console.log(err)
          })
        }
      },
    },

    created() {
      this.communityId = this.$route.params.communityId
    }
  }
</script>