<template>
	<div>
    <template>
        <v-text-field
          v-model="title"
          :counter="50"
          :rules="titleRules"
          label=" "
          solo
          required
        ></v-text-field>

        <v-row style="border-bottom: 1px solid #979797; margin: 0px 0px 10px 0px;">
          <v-col
            cols="12"
            sm="6"
            md="6"
            style="padding: 12px 12px 12px 0px;"
          >
            <h5>/</h5>
            <v-card style="padding: 3px; height: 32px;">
              <input style="border: 1px solid #979797;" type="date" v-model="date">
              <input style="border: 1px solid #979797; margin-left: 4px;" type="time" step="3600000" v-model="time">
            </v-card>
          </v-col>
          
          <v-col
            cols="12"
            sm="6"
            md="6"
            style="padding: 12px 0px 12px 12px;"
          >
            <h5></h5>
            <v-card style="padding: 3px; height: 32px;">
              <input style="width: 100%;" type="number" v-model="maxUser">
            </v-card>
          </v-col>
        </v-row>

        <h3 style="color: #FF0000;">
          **
        </h3>
        <ul style="list-style-type: square; color: #979797; margin-left: 20px; margin-top: 10px;">
          <li>1</li>
          <li>2</li>
          <li>3</li>
          <li>4</li>
          <li>5</li>
        </ul>
        
        <div style="display: flex; justify-content: center; margin-top: 30px;">
          <v-btn @click="goBack()" style="background-color: #979797; color: #FFFFFF; margin: 0 10px;"></v-btn>
          <v-btn @click="submit()" style="background-color: #797BF8; color: #FFFFFF; margin: 0 10px;"></v-btn>
        </div>
    </template>
	</div>
</template>

<script>
  export default {
    name: 'CreateMeetingForm',
    data: function() {
      return {
        title: '',
        titleRules: [
          v => !!v || ' .',
          v => (v && v.length <= 50) || ' 50  .'
        ],
        date: '',
        time: '',
        maxUser: '',
        openDate: '',
      }
    },

    methods: {
      goBack () {
        this.$router.push({ name: 'MyMeetingList' })
      },
      submit () {
        const meetingItem = {
          title: this.title,
          openDate: this.date + ' ' + this.time,
          maxUser: this.maxUser,
        }
        if (meetingItem.title) {
          this.$axios({
            method: 'post',
            url: `http://localhost:8080/api/v1/meetings`,
            data: meetingItem,
          })
          .then(res => {
            console.log(res)
            this.title = ''
            this.openDate = ''
            this.maxUser = ''
            this.$router.push({ name: 'MyMeetingList' })
          })
          .catch(err => {
            console.log(meetingItem)
            console.log(err)
          })
        }
      }
    },
  }
</script>