<template>
  <div>
    <img src="../assets/sidemanagercheck.png" alt="check-manager" style="padding: 6% 0 2% 2%;">
    <div class="container">
      <h1 style="padding: 0 0 2% 0; font-size: 2.5em;"> </h1>
      <button @click="checkManager()" style="padding: 0.7% 7% 0.7% 7%; background-color: #797BF8; color: #FFFFFF"></button>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'CheckManager',
    methods: {
      checkManager() {
        this.$axios({
          method: 'get',
          url: `http://localhost:8080/api/v1/meetings/isManager`
        })
        .then(res => {
          console.log(res)
          if (res.data === "SUCCESS") {
            this.$router.push({name: 'MyMeetingList'})
          } else {
            alert(' .   timeroom@ssafy.com .')
            this.$router.push({name: 'Index'})
          }
        })
        .catch(err => {
          alert('  .')
          this.$router.push({name: 'Index'})
        })
      },
    }
  }
</script>

<style scoped>
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
</style>